.flag{perspective:400px;position:relative}.flag *{-webkit-transform-style:preserve-3d;transform-style:preserve-3d;transform-origin:top left}.flag-segment-wrapper,.flag-segment,.flag-segment-overlay{position:absolute;height:100%;top:0}.flag-segment{width:100%;left:100%}.flag-segment:before{content:'';position:absolute;height:100%;width:calc(100% + 1px);background:red}.flag-segment-wrapper>.flag-segment{left:0}.flag-segment-overlay{width:100%}.flag-segment-light{background:rgba(255,255,255,0.5)}.flag-segment-dark{background:rgba(0,0,0,0.5)}
